import axios from 'axios'
import { Message, MessageBox } from 'element-ui'
//import store from '@/store'
//token
import { getToken } from '@/utils/auth'
import qs from 'qs'

// axios
const service = axios.create({
  baseURL: process.env.BASE_API, // apibase_url
  timeout: 30000, // 
  // withCredentials: true
  /* headers: {
    "Content-Type": "application/x-www-form-urlencoded"
  } */
})

// request interceptor 
service.interceptors.request.use(config => {
  // POST
  if(config.method  === 'post'&& config.data.constructor !== FormData) {
    config.data = qs.stringify(config.data)
  }
  // Do something before request is sent
  if (getToken()) {
    // token-- ['tokenKey]key 
    //config.headers['tokenId'] = getToken()
  }
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone interceptor 
service.interceptors.response.use(
  /**
   * responsecode
   * code
   * xmlhttprequest error
   * 
   */
  response => { // Status Code:200
//     const res = response.data
//     if (res.code !== 200) { // code != 200
//       Message({
//         message: res.message,
//         type: 'error',
//         duration: 5 * 1000
//       })
//       // 50008:token; 50012:;  50014:Token ;
//       if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
//
//         MessageBox.confirm('', '', {
//           confirmButtonText: '',
//           cancelButtonText: '',
//           type: 'warning'
//         }).then(() => {
//           store.dispatch('FedLogOut').then(() => {
//             location.reload() // vue-router bug
//           })
//         })
//       }
//       return Promise.reject(res.msg)
//     } else { // code: 200
	console.log(response)
    return response.data
//     }
  },
	error => { // Status Code:500 502
    console.log('err' + error) // for debug
    Message({
      message: error.message,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(error)
})

export default service


